* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.country-card__hero {
  display: flex;
  align-items: center;
}

.country-card__name {
  margin: 0;
  padding: 0;
  font-size: 40px;
  font-weight: 400;
  margin-left: 20px;
}

.country-card__text {
  font-weight: bold;
}

.country-list__item {
  display: flex;
  align-items: center; 
}

.country-list__name {
  font-weight: 400;
  margin-left: 10px;
}

.value {
  font-weight: normal;
  font-size: 20px;
}
